import React, {useState, useRef, useEffect} from 'react'
import { Card, Button } from 'react-bootstrap'
export default function SelectedFunction({func, newFunc}) {
  const inputRef = useRef();
  const [input, setInput] = useState([]);
  const [output, setOutput] = useState([]);
  const axios = require('axios').default;
  let funcState = newFunc
  useEffect(() => {
    (input.length != 0) ? sendAndRecieveReq() : console.log("loading", input)
  }, [input])
  
  const sendAndRecieveReq = async () => {
    try{
      funcState = false;
      const response = await axios.get(`https://comparator-api.herokuapp.com${func.endPoint}?arg=${input}`);
      console.log(response.data);
      let data = `${response.data}`
      data = data.toUpperCase();
      setOutput(<div>{data}</div>)
    }
    catch (error){
      console.log("ERROR ", error)
    }
  }
  const determineText = () => {
    let name;
    switch (func.name) {
      case "sort":
        name = "Array"
        break;
      case "create":
        name = "Number"
        break;
      default:
        name = "Comparator Network"
        break;
    }
    return `Enter ${name} here:\t\t`
  }
  const handleSubmit  = () => {
    const newInput = inputRef.current.value
    if(newInput == ''){return}
    setInput(newInput);
    console.log(newInput)
    console.log("type of", typeof newInput)
    inputRef.current.value = null
  }
  return (
    <Card style={{ width: '40rem' }}> 
      <Card.Body>
        <Card.Title variant="primary">
          {func.name.toUpperCase()}
        </Card.Title>
        <Card.Text>
        {`Input: ${func.input}`}
        <br/>
        {`Output: ${func.output}`}
        <br/>
        {`Description: ${func.description}`}
        <br/>
        {determineText()}
        <input ref={inputRef} type={"text"}/>
      </Card.Text>
      <Button onClick={handleSubmit}>Submit</Button>
      <br/>
      <br/>
      <div>{ output}</div>
      </Card.Body>
    </Card>
  )
}
